import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { setMaterialAction } from "../../../redux-toolkit/slice/action";
import "./NavBar.css";
import { BASE_URL } from "../../../../config";

function NavBar() {
  const { nextPage } = useSelector((state) => state?.taskpane);
  const dispatch = useDispatch();
  const { materialAction } = useSelector((state) => state?.action);
  return (
    <div className="navbar">
      <div className="navbar-logo">
        <img src={`${BASE_URL}/assets/beacon-without-background -logo.png `} />
      </div>
      {nextPage === "material" && (
        <div className="navbar-option">
          <span
            className={`navbar-button ${materialAction == "get" ? "selectedAction" : null}`}
            onClick={() => dispatch(setMaterialAction("get"))}
          >
            Get
          </span>
          <span
            className={`navbar-button ${materialAction == "map" ? "selectedAction" : null}`}
            onClick={() => dispatch(setMaterialAction("map"))}
          >
            Map
          </span>
          <span
            className={`navbar-button ${materialAction == "export" ? "selectedAction" : null}`}
            onClick={() => dispatch(setMaterialAction("export"))}
          >
            Export
          </span>
          <span
            className={`navbar-button ${materialAction == "update" ? "selectedAction" : null}`}
            onClick={() => dispatch(setMaterialAction("update"))}
          >
            Update
          </span>
        </div>
      )}
    </div>
  );
}

export default NavBar;
