import React, { useEffect, useState } from "react";
import "./Project.css";
import { DTX_BASE_URL, DTX_GRAPHQL_URL } from "../../../config";
import axios from "axios";
import { useDispatch, useSelector } from "react-redux";
import { setActiveProject, setNextPage } from "../../redux-toolkit/slice/TaskpaneSlice";
import { setUploadLoader } from "../../redux-toolkit/slice/action";
import { logout, showErrorMessage } from "../../constFunctions/copmponentFunctions";
import { JWT_ERROR_MESSAGE } from "../../constFunctions/constants";
import ProjectIndicator from "../ProjectIndicator/ProjectIndicator";
import SearchIcon from "../../../../assets/icons/search-icon.svg";
function Project() {
  const [projectList, setProjectList] = useState();
  const [searchQuery, setSearchQuery] = useState("");
  const [filteredData, setFilteredData] = useState(projectList);
  const dispatch = useDispatch();
  const { activeProject } = useSelector((state) => state.taskpane);
  const { uploadLoader, sideBarOpen } = useSelector((state) => state.action);
  const dispacth = useDispatch();
  useEffect(() => {
    const filtered = projectList?.filter((item) => item.name.toLowerCase().includes(searchQuery.toLowerCase()));
    setFilteredData(filtered);
  }, [projectList, searchQuery]);

  // while loading the componenetd it fetch the available projects in dtx
  useEffect(() => {
    dispacth(setUploadLoader(true));
    const token = localStorage.getItem("tenantToken");
    try {
      const body = {
        operationName: "MyQuery",
        query: "query MyQuery {\n  core_projects {\n    name\n    id\n  }\n}\n",
        variables: null,
      };
      fetch(DTX_GRAPHQL_URL, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify(body),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data.errors) {
            data.errors.map((element) => {
              if (element?.message.includes(JWT_ERROR_MESSAGE)) {
                logout();
                dispatch(setNextPage("homeLogin"));
              } else {
                showErrorMessage("bom-error-message", "Something went wrong");
              }
            });
          } else {
            setProjectList(data?.data?.core_projects);
            dispacth(setUploadLoader(false));
          }
        })
        .catch((error) => {
          console.log(error);
          dispacth(setUploadLoader(false));
        });
    } catch (error) {
      console.log(error);
      dispacth(setUploadLoader(false));
    }
  }, [true]);

  // getProjectDeatails ==> get all the details of selelcted project by passing projectId
  const getProjectDetails = (projectDetails) => {
    console.log(projectDetails);
    dispacth(setUploadLoader(true));
    try {
      const headers = {
        "Content-Type": "application/json",
        Authorization: "Bearer " + localStorage.getItem("beaconToken"),
      };
      axios
        .post(
          `${DTX_BASE_URL}/user/tokenExchange`,
          { projectId: projectDetails?.id },
          {
            headers: headers,
          }
        )
        .then((response) => {
          localStorage.setItem("projectExchangeToken", response?.data?.message);
          dispatch(setActiveProject(projectDetails));
          dispacth(setUploadLoader(false));
        })
        .catch((error) => {
          console.log("tenant  login ", error);
          dispacth(setUploadLoader(false));
        });
    } catch (error) {
      console.log(error);
      dispacth(setUploadLoader(false));
    }
  };

  // setActiveProjectAsMaster ==> if select master instead of any project then project token will be saved as tenant token
  const setActiveProjectAsMaster = () => {
    dispatch(setActiveProject({ name: "Master", id: "Master" }));
    localStorage.setItem("projectExchangeToken", localStorage.getItem("tenantToken"));
  };
  return (
    <div className="project-body">
      <ProjectIndicator />
      {/* {activeProject && (
        <p className="active-project-name">
          Active Project :<span> {activeProject?.name}</span>{" "}
        </p>
      )} */}
      <span className="search-box">
        {!sideBarOpen && <img className="search-icon" src={SearchIcon} />}
        <input
          type="search"
          className="project-search-input"
          placeholder="Search by name"
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
        />
      </span>
      <div className="project-list-box">
        {searchQuery ? (
          "master".includes(searchQuery) && (
            <label
              className={`project-list-names ${activeProject?.id === "Master" ? "selected-project" : ""}`}
              onClick={setActiveProjectAsMaster}
            >
              0. Master
            </label>
          )
        ) : (
          <label
            className={`project-list-names ${activeProject?.id === "Master" ? "selected-project" : ""}`}
            onClick={setActiveProjectAsMaster}
          >
            0. Master
          </label>
        )}
        {filteredData?.map((project, index) => (
          <label
            key={project?.id}
            className={`project-list-names ${activeProject?.id === project?.id ? "selected-project" : ""}`}
            onClick={() => {
              getProjectDetails(project);
            }}
          >
            {index + 1}. {project?.name}
          </label>
        ))}
      </div>
    </div>
  );
}

export default Project;
